import React, { Component } from 'react'
import {Card, Button, Radio,message} from 'antd';
import styles from "./index.module.less";
import {parseUrlSearch} from './util'
import {getAction,postAction } from '@/axios';
import FlowModal from './newFlowModal'
import PreviewWorkFlow from './previewWorkFlow';
export default class Step4 extends Component {

    state = {
        fillProcessId: null,
        list: [],
        param:null,
        visible:false
    };
    componentDidMount() {
        this.getFlows()
        let param=parseUrlSearch()
        if(param.id){
            this.setState({
                param
            },()=>{
                this.getData()                
            })
        }
    }
    getData=()=>{
        let {param}=this.state
        getAction('/ybt/form/info',{
            formId:param.id
        }).then(res=>{
            if(res.success){
                this.setState({
                    fillProcessId:res.obj.fillProcessId || '',
                })
            }
        });
        
    }
    getFlows=()=>{
        postAction(`/ybt/wf/process/queryFolderProcessList?pageNum=1&pageSize=0`,{
            processType:3,
            status:1
        }).then(res=>{
            if(res.success){
                this.setState({
                    list:res.obj.rows || []
                })
            }
        })
    }
    onChange = e => {
        this.setState({ fillProcessId: e.target.value });
    };

    toPre = () => {
        this.props.history.push(`/formManagement/createForm?id=${this.state.param.id}`)
    }

    toNext = () => {
        // 此处需要检查是否有设计过表单

        if(!this.state.fillProcessId){
            message.warning('请选择工作流程')
            return
        }
        // 根据流程id查出流程名字
        let name=this.state.list.find(item=>item.processId === this.state.fillProcessId).processName
        let params={
            "formId": this.state.param.id,
            "fillProcessName":name,
            "fillProcessId": this.state.fillProcessId,
        }
        postAction('/ybt/form/save',params).then(res=>{
            if(res.success){
                // 保存之后查询表单模板是否已经设置
                let {param}=this.state
                getAction('/ybt/form/info',{
                    formId:param.id
                }).then(res=>{
                    if(res.success){
                        if(res.obj.columnProperties){
                            this.props.history.push(`/formManagement/uploadTemplate?id=${this.state.param.id}`)
                        }else{
                            message.warning('请先设计表单')
                        }
                        // this.props.history.push(`/formManagement/uploadTemplate?id=${this.state.param.id}`)  
                    }
                });         
            }else{
                message.error(res.obj)   
            }
        }) 
    }
    toDesign=()=>{
        let url=window.location.href.split('setWorkFlow')[0]+'designForm?id='+this.state.param.id
        window.open(url) 
    }
    toCancle = () => {
        this.props.history.push('/formManagement')
    }
    addWorkFlow = () => {
        this.setState({
            visible:true
        })
    }
    closeFlow=()=>{
        this.setState({
            visible:false
        })
    }
    render() {
        const radioStyle = {
            display: 'block',
            height: '30px',
            lineHeight: '30px',
        };
        return (
            <div style={{padding:"20px"}}>
                {/* 新增工作流程的弹窗 */}
                {
                    this.state.visible && <FlowModal ids={this.state.param.id} visible={this.state.visible} closeFlow={this.closeFlow}/>
                }
                
                <Card  title={`选择工作流程-${window.sessionStorage.getItem('formManageName')}`} style={{ width: "100%", boxShadow:"0px 0px 2px 2px #f1f1f1" }} bodyStyle={{padding:"0px"}}>
                    <div className={styles.workFlowContainer}>
                        <div className={styles.left}>
                            <p>请从以下工作流程中选择：</p>
                            {
                                this.state.list && this.state.list[0]?
                                <Radio.Group onChange={this.onChange} value={this.state.fillProcessId}>
                                    {
                                        this.state.list.map(item => <Radio key={item.processId} style={radioStyle} value={item.processId}>
                                                                        {item.processName}
                                                                    </Radio>
                                        )
                                    }
                                </Radio.Group>
                                :
                                null
                            }
                            <Button style={{marginTop:20}} type="primary" icon="plus" onClick={this.addWorkFlow}>新增工作流程</Button>
                        </div>
                        <div className={styles.right}>
                            <div className={styles.workPlace}>
                                <PreviewWorkFlow processId={this.state.fillProcessId}/>
                            </div>
                            <div className={styles.buttons}>
                                <Button  type="primary"  style={{marginRight:10}} ghost onClick={this.toPre}>上一步</Button>
                                <Button type="primary" style={{marginRight:10}} onClick={this.toNext}>下一步</Button>
                                <Button type="primary" style={{marginRight:10}} onClick={this.toDesign}>设计表单</Button>
                                <Button onClick={this.toCancle}>取消</Button>
                            </div>
                        </div>
                    </div>
                </Card>
            </div>
        )
    }
}
