import React, { Component } from 'react'
import {Input, Select,  message, Modal, TreeSelect } from 'antd'
import { getAction } from '@/axios';
import {withRouter} from 'react-router-dom'
const { Option } = Select;
const { TreeNode } = TreeSelect;
class NewFlowModal extends Component {
    state={
        newFlowName: '', // 新建流程名称
        newFlowType: '3', // 新建流程类型
        selectedDoc: '', // 新建流程时， 选择的文件夹
        treeData: [ // 新建流程时， 选择的文件夹数据
        ]
    }
    // 确认新建流程
    buildFlowOk = () => {
        if (!this.state.newFlowName) {
            message.warn('请输入新建流程名称')
            return  false
        } else if (!this.state.newFlowType) {
            message.warn('请选择新建流程类型')
            return false
        } else if (!this.state.selectedDoc) {
            message.warn('请选择所属文件夹')
            return false
        }
        window.localStorage.setItem(
            "flowInfo",
            JSON.stringify({
                type:this.state.newFlowType,
                flowName:this.state.newFlowName,
                folderId:this.state.selectedDoc
            })
        );
        window.localStorage.setItem(
            "flowInfo",
            JSON.stringify({
                type:this.state.newFlowType,
                flowName:this.state.newFlowName,
                folderId:this.state.selectedDoc
            })
        );
        this.props.history.push(`/workFlowDesign?url=formManagement&id=${this.props.ids}`)
    }
    hideBuildFlow=()=>{
        this.props.closeFlow()
    }
    componentDidMount() {
        this.getTreeData();
    }
    //获取树的结构
    getTreeData = () => {
        getAction("/ybt/wf/process/queryFolderTree").then(res => {
            if(res.success){
                this.setState({
                    treeData:res.obj || []
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //渲染树
    renderTreeNodes = data => data.map(item => {
        if (item.children) {
        return (
            <TreeNode title={item.title} key={item.id+"key"} value={item.id}>
                {this.renderTreeNodes(item.children)}
            </TreeNode>
        );
        }
        return <TreeNode key={item.id+"key"} title={item.title} value={item.id}/>;
    });
    // 新建流程名称输入框
    newFlowNameChange = (e) => {
        this.setState({
            newFlowName: e.target.value
        })
    }
    // 新建流程类型下拉框
    newFlowTypeChange = (value) => {
        this.setState({
            newFlowType: value
        })
    }
    // 新建流程时， 选择文件夹下拉选择
    selectedDocChange = value => {
        this.setState({
            selectedDoc:value
        })
    }
    render() {
        return (
            <Modal
                title='新建流程'
                // this.props.visible
                visible={this.props.visible}
                onOk={this.buildFlowOk}
                onCancel={this.hideBuildFlow}
                okText='确定'
                cancelText='取消'
            >
                <div style={{ marginBottom: '10px' }}>
                    <span style={{ width: '100px', float: 'left', lineHeight: '32px' }}>流程名称：</span>
                    <Input style={{ width: '300px' }} value={this.state.newFlowName} onChange={this.newFlowNameChange} placeholder='请输入新建流程名称...' />
                </div>

                <div style={{ marginBottom: '10px' }}>
                    <span style={{ width: '100px', float: 'left', lineHeight: '32px' }}>流程类型：</span>
                    <Select value={this.state.newFlowType} style={{ width: 300 }} onChange={this.newFlowTypeChange} >
                        {/* <Option value="1">创建计划</Option>
                        <Option value="2">新增表单</Option> */}
                        <Option value="3">填报内容</Option>
                    </Select>
                </div>

                <div style={{ marginBottom: '10px' }}>
                    <span style={{ width: '100px', float: 'left', lineHeight: '32px' }}>所属文件夹：</span>
                    <TreeSelect
                        style={{ width: '300px' }}
                        value={this.state.selectedDoc}
                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                        // treeData={this.state.treeData}
                        placeholder="请选择所属文件夹"
                        treeDefaultExpandAll
                        onChange={this.selectedDocChange}
                    >
                        {this.renderTreeNodes(this.state.treeData)}
                    </TreeSelect>
                </div>
            </Modal>
        )
    }
}


export default withRouter(NewFlowModal)
